export default function defence() {
	if (checkHorizontal()) {
		return true;
	} else if (checkVertical()) {
		return true;
	} else if (checkDiagonal("a1", "b2", "c3")) {
		return true;
	} else if (checkDiagonal("c1", "b2", "a3")) {
		return true;
	} else {
		return false;
	}
}

function checkHorizontal() {
	const rows = ["a", "b", "c"];
	let found = false;

	rows.forEach((row) => {
		const first = document.getElementById(row + "1");
		const second = document.getElementById(row + "2");
		const third = document.getElementById(row + "3");

		const arr = [first, second, third];
		const player1Blocks = arr.filter((item) => parseInt(item.value) === 1)
			.length;
		const undefinedBlock = arr.filter((item) => item.value === undefined);
		if (player1Blocks === 2 && undefinedBlock.length === 1) {
			check(undefinedBlock[0]);
			found = true;
		}
	});

	return found;
}

const checkVertical = () => {
	const columns = ["1", "2", "3"];

	let found = false;

	columns.forEach((column) => {
		const first = document.getElementById("a" + column);
		const second = document.getElementById("b" + column);
		const third = document.getElementById("c" + column);

		const arr = [first, second, third];
		const player1Blocks = arr.filter((item) => parseInt(item.value) === 1)
			.length;
		const undefinedBlock = arr.filter((item) => item.value === undefined);
		if (player1Blocks === 2 && undefinedBlock.length === 1) {
			check(undefinedBlock[0]);
			found = true;
		}
	});

	return found;
};

const checkDiagonal = (a, b, c) => {
	let found = false;
	const first = document.getElementById(a);
	const second = document.getElementById(b);
	const third = document.getElementById(c);

	const arr = [first, second, third];
	const player1Blocks = arr.filter((item) => parseInt(item.value) === 1).length;
	const undefinedBlock = arr.filter((item) => item.value === undefined);
	if (player1Blocks === 2 && undefinedBlock.length === 1) {
		check(undefinedBlock[0]);
		found = true;
	}

	return found;
};

const check = (r) => {
	r.innerHTML = '<p class="display-1 t-shad font-weight-bold">X</p>';
	r.value = 2;
};
