import React, { useState, useEffect, useCallback } from "react";
import { Row, Col, Button } from "reactstrap";
import "../Logic/AI";
import Play from "../Logic/AI";

export default function Tictactoe() {
	const [count, setCount] = useState(1);

	const [player, setPlayer] = useState(1);

	let winner = false;

	const [player1Games, setPlayer1Games] = useState(0);
	const [player2Games, setPlayer2Games] = useState(0);

	useEffect(() => {
		const Player1 = window.localStorage.getItem("player1");
		const Player2 = window.localStorage.getItem("player2");
		if (Player1 !== null) {
			setPlayer1Games(parseInt(Player1));
		}
		if (Player2 !== null) {
			setPlayer2Games(parseInt(Player2));
		}
	}, []);

	const setContent = (e) => {
		let val = e.target.innerHTML;
		if (val === "" && count <= 9 && player === 1) {
			e.target.value = player;
			e.target.innerHTML = '<p class="display-1 t-shad font-weight-bold">O</p>';
			setCount(count + 1);
			togglePlayer();
			checkWinner();
		}
	};

	const checkDraw = () => {
		if (count === 9 && winner === false)
			if (!alert("It's a draw")) {
				window.location.reload();
				return true;
			}
	};

	const checkWinner = () => {
		//horizontal check
		checkHorizontal();
		checkVertical();
		checkDiagonal("a1", "b2", "c3");
		checkDiagonal("c1", "b2", "a3");

		checkDraw();
	};

	const togglePlayer = () => {
		if (player === 1) {
			setPlayer(2);
		} else {
			setPlayer(1);
		}
	};

	const checWinnerCallBack = useCallback(checkWinner, [player]);
	const togglePlayerCallBack = useCallback(togglePlayer, [player]);

	useEffect(() => {
		if (player === 2 && count <= 9 && count !== 10) {
			Play();
			setCount(count + 1);
			togglePlayerCallBack();
			checWinnerCallBack();
		}
	}, [player, count, checWinnerCallBack, togglePlayerCallBack]);

	const reset = () => {
		setPlayer1Games(0);
		setPlayer2Games(0);
		window.localStorage.removeItem("player1");
		window.localStorage.removeItem("player2");
		window.location.reload();
	};

	const checkHorizontal = () => {
		const rows = ["a", "b", "c"];

		// for (let row = 0; row < rows.length; row++) {
		// 	const first = document.getElementById(rows[row] + "1");
		// 	const second = document.getElementById(rows[row] + "2");
		// 	const third = document.getElementById(rows[row] + "3");

		// 	check(first, second, third);
		// }

		rows.forEach((row) => {
			const first = document.getElementById(row + "1");
			const second = document.getElementById(row + "2");
			const third = document.getElementById(row + "3");

			check(first, second, third);
		});
	};
	const checkVertical = () => {
		const columns = ["1", "2", "3"];

		columns.forEach((column) => {
			const first = document.getElementById("a" + column);
			const second = document.getElementById("b" + column);
			const third = document.getElementById("c" + column);

			check(first, second, third);
		});
	};

	const checkDiagonal = (a, b, c) => {
		const first = document.getElementById(a);
		const second = document.getElementById(b);
		const third = document.getElementById(c);

		check(first, second, third);
	};

	const check = (first, second, third) => {
		if (
			first.value !== undefined &&
			first.value === second.value &&
			second.value === third.value
		) {
			first.classList.add("red");
			second.classList.add("red");
			third.classList.add("red");
			if (!alert("Player " + first.value + " is the winner")) {
				window.location.reload();
			}
			setCount(10);
			if (first.value === 1) {
				window.localStorage.setItem("player1", player1Games + 1);
			} else {
				window.localStorage.setItem("player2", player2Games + 1);
			}
			winner = true;
		}
		// else if (count === 9) {
		// 	setCount(10);
		// 	if (!alert("It's a draw")) {
		// 		window.location.reload();
		// 		return true;
		// 	}
		// }
	};
	return (
		<React.Fragment>
			<div style={{ height: "7vh" }} className="d-flex align-items-center">
				<span className="float-left ml-2">Player {player}'s turn</span>
				<span className="m-auto">
					Games won 1:{player1Games} 2:{player2Games}
				</span>
				<Button
					color="primary"
					size="sm"
					className="m-auto"
					onClick={() => {
						reset();
					}}
				>
					RESET
				</Button>
			</div>
			<Row style={{ height: "30vh" }}>
				<Col
					id="a1"
					onClick={setContent}
					className="h-100 w-100 border d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="a2"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="a3"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
			</Row>
			<Row style={{ height: "30vh" }}>
				<Col
					id="b1"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="b2"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="b3"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
			</Row>
			<Row style={{ height: "30vh" }}>
				<Col
					id="c1"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="c2"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
				<Col
					id="c3"
					onClick={setContent}
					className="h-100 w-100 border  d-flex align-items-center justify-content-center pad"
				></Col>
			</Row>
		</React.Fragment>
	);
}
