export function Random() {
	const rows = ["a", "b", "c"];
	const columns = ["1", "2", "3"];

	let emptyCells = [];

	rows.forEach((row) => {
		columns.forEach((column) => {
			const cell = document.getElementById(row + column);

			if (cell.value === undefined) {
				emptyCells = (emptyCells, [...emptyCells, cell]);
			}
		});
	});

	const pickedCell = Math.floor(Math.random() * (emptyCells.length - 0));

	emptyCells[pickedCell].innerHTML =
		'<p class="display-1 t-shad font-weight-bold">X</p>';
	emptyCells[pickedCell].value = 2;
}
